import React from "react";
import {
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
    MDBListGroup,
    MDBListGroupItem,
    MDBCardHeader,
    MDBCardFooter,
    MDBBtn,
    MDBCol,
    MDBRow,
    MDBNav,
    MDBNavItem,
    MDBNavLink,
    MDBCardGroup,
    MDBContainer,
    MDBEdgeHeader,
    MDBAnimation,
    MDBJumbotron
} from "mdbreact";
import DocsLink from "../components/docsLink";
import SectionContainer from "../components/sectionContainer";

const Gallery = () => {
    const panelStyle = { width: "22rem" };
    return (
        <>
            <MDBEdgeHeader color="indigo darken-3" className="sectionPage" />
            <MDBAnimation type="zoomIn" duration="500ms">
                <MDBContainer>
                    <MDBRow>
                        <MDBCol md="8" className="mx-auto">
                            <MDBJumbotron className="mt-3">
                                <h1 className="text-center">
                                    {/* <MDBIcon icon="quote-left" border className="indigo-text mr-w" pull="left" /> */}
                                    Portifólio
                                 </h1>
                            </MDBJumbotron>
                        </MDBCol>
                    </MDBRow>
                </MDBContainer>
            </MDBAnimation>
            <MDBContainer>

                <SectionContainer>
                    <MDBCardGroup deck className="mt-4">
                        <MDBCard>
                            <MDBCardBody>
                                <MDBCardTitle tag="h5">Panel title</MDBCardTitle>
                                <MDBCardText>
                                    This is a wider panel with supporting text below as a natural lead-in to additional content. This
                                    content is a little bit longer.
                                </MDBCardText>
                            </MDBCardBody>
                            <MDBCardFooter small muted>
                                Last updated 3 mins ago
                            </MDBCardFooter>
                        </MDBCard>
                    </MDBCardGroup>
                </SectionContainer>

            </MDBContainer>
        </>
    );
};

export default Gallery;
